*{
    margin: 0px;
}
.body{
    box-sizing: border-box;
}
.main-bg{
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;
    background-image: radial-gradient(#F0F0F0,#E8E8E8,#C8C8C8,#A8A8A8,#888888);
}
.small-card{
    display: flex;
    flex-direction: row;
    height: 180px;
    width: 60%;
    align-items: center;
    border-top-right-radius: 20px;
    border-top-left-radius: 20px;
    border-bottom-left-radius: 20px;
    border-bottom-right-radius: 20px;
    margin-top: 32px;
    border: 2px grey solid;
    background-color: white;
}

.text-bg{
    display: flex;
    flex-direction: column;
    padding-left: 100px;
}
.image{
    height: 180px;
    width: 240px;
    border-top-left-radius: 18px;
    border-bottom-left-radius: 18px;
}
.para{
    font-family: raleway;
    font-size: 18px;
    font-weight: 500;
    padding-top: 10px;
    padding-bottom: 10px;
}
.head{
    font-family: nunito;
    font-size: 20px;
}
.btn{
    width: 90px;
    height: 28px;
    margin-right: 30px;
}
.btn-bg{
    display: flex;
    flex-direction: row;
    
    
}
.heading{
    font-size: 24px;
    margin-top: 30px;
    font-family:raleway; 
}
.confirm-para{
    color: green;
    font-weight: 400;
    margin-top: 10px;
}

@media (max-width: 768px){
    .main-bg{
        margin-left: 25px;
    }
    .small-card{
        height: 380px;
        width: 80vw;
        flex-direction: column;
    }
    .image{
        height: 220px;
        width: 80vw;
        border-top-right-radius: 20px;
        border-top-left-radius: 20px;
        border-bottom-left-radius: 0px;
        border-bottom-right-radius: 0px;

    }
    .text-bg{
        padding: 20px;
        padding-left: 20px;
        
    }
    .btn-bg{
        display: flex;
        flex-direction: row;
        
        
    }
    .head{
        font-size: 20px;
    }
    .para{
        font-size: 14px;
        width: 70vw;
    }
    .btn{
        margin-right: 10px;
    }
    .confirm-para{
        margin-top: 0px;
    }
}
@media (min-width: 768px){
    .main-bg{
        display: flex;
        flex-direction: column;
        justify-content: center;
        align-items: center;
    }
    .form{
        margin-left: 400px;
    }
    .submit-btn{
        margin-left: 200px;
        margin-top: 20px;
        margin-bottom: 40px;
    }
}
.submit-btn{
    width: 120px;
    height: 35px;
    background-color: #1560bd;
    color: white;
    border: 0px;
    border-radius: 4px; 
    cursor: pointer;

}
.vote{
    background-color: 	#50C878;
    width: 90px;
    border: 0px;
    border-radius: 4px;
    font-weight: 600;
    
    letter-spacing: 1px;
    cursor: pointer;
}
.view{
    
    width: 90px;
    border: 2px solid 	#50C878;
    border-radius: 4px;
    font-weight: 600;
    background-color: white;
    cursor: pointer;
    
}
.radios{
    margin-top:20px;
}
.radio1{
    color:blue;
}
