*{
    margin: 0;
}
.body{
    box-sizing: border-box;
}
.bg-container{
    
    display: flex;
    flex-direction: column;
    justify-content: center;
    background-color: #D3D3D3;
    max-width: 100vw;
    height: 100vh;
    
}
@media (min-width: 768px){
    .bg-container{
        padding-left: 200px;
    }
}

.heading{
    font-family: Sans-serif;
    font-weight: 600;
}
.error{
    font-family: Sans-serif;
    font-weight: 600;
    color: red;
}
.scanner{
    display: flex;
    flex-direction: row;
}
    
